































































































import { Component, Vue, Ref, Prop } from 'vue-property-decorator';

@Component({
  components: {}
})
export default class BusinessScene extends Vue {
  @Ref() workRuleForm: any;
  @Prop({ default: false }) addBusinessDialog: boolean;
  @Prop({ default: [] }) readonly addScenesTags: Array<any>;
  @Prop({ default: [] }) readonly queryDeptData: Array<any>;
  @Prop({ default: [] }) readonly dictsData: Array<any>;
  private addTaskShow = false;
  private ruleForm: any = {
    businessName: '',
    businessSection: '',
    businessDescribe: '',
    businessSubscibe: '',
    businessTag: []
  };
  private rules: any = {
    businessName: [
      { required: true, message: '请输入场景名称', trigger: 'blur' }
    ],
    businessSection: [
      { required: true, message: '请选择所属部门', trigger: 'change' }
    ]
  };
  // 新增业务场景 确定
  addBusinessSave() {
    this.workRuleForm.validate((valid) => {
      if (valid) {
        this.addTaskShow = true;
      }
    });
  }
  // 新增业务场景 取消
  addBusinessCancel() {
    this.addBusinessDialog = false;
    this.workRuleForm.resetFields();
    this.$emit('businessSceneClick', this.addBusinessDialog);
  }
  // 新增业务、项目创建弹窗关闭
  handleCloseBtn(done) {
    this.addTaskShow = false;
  }
  // 新增业务场景 提示弹窗 添加任务
  addTaskSave() {
    this.$router.push({
      path: '/taskConfigCenter/addTasks',
      query: {
        scenesDept: this.ruleForm.businessSection,
        scenesDesc: this.ruleForm.businessDescribe,
        scenesName: this.ruleForm.businessName,
        subscribeUser: this.ruleForm.businessSubscibe,
        tagId: this.ruleForm.businessTag,
        sceneFlag: 'true'
      }
    });
    this.addTaskShow = false;
  }
}
