<template>
  <div class="workHandover">
    <!-- 工作交接弹框 -->
    <simple-dialog
      class="dialogClass"
      dialogTitle="工作交接"
      :dialog-visible="workConnectDialog"
      botton-show="true"
      cancelText="取消"
      saveText="确定"
      @saveEmit="workConnectSave('ruleForm')"
      @cancelEmit="workConnectCancel('ruleForm')"
      dialogWidth="20%"
    >
      <el-button
        type="text"
        style="position: absolute;left: 70%;top: 2%;"
        @click="historyBtn"
        >历史交接</el-button
      >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="工作交接人:" prop="region">
          <el-select v-model="ruleForm.region" placeholder="请选择" filterable>
            <el-option
              v-for="item in dictsData"
              :key="item.usercode"
              :label="item.username"
              :value="item.usercode"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交接场景:" prop="content">
          <el-select
            v-model="ruleForm.content"
            multiple
            collapse-tags
            placeholder="请选择交接场景"
          >
            <el-option
              v-for="item in handoverData"
              :key="item.id"
              :label="item.scenesName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开始时间:" prop="startValue">
          <el-date-picker
            v-model="ruleForm.startValue"
            type="datetime"
            placeholder="选择日期时间"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间:" prop="endValue">
          <el-date-picker
            v-model="ruleForm.endValue"
            type="datetime"
            placeholder="选择日期时间"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
    </simple-dialog>
    <!-- 历史交接弹框 -->
    <simple-dialog
      class="dialogClass"
      dialogTitle="历史工作交接"
      :dialog-visible="historyConnectDialog"
      botton-show="true"
      cancelText="取消"
      saveText="确定"
      @saveEmit="historyConnectSave"
      @cancelEmit="historyConnectCancel"
      dialogWidth="60%"
    >
      <base-table-panel
        noTitle
        class="sortTable noTableBorder"
        :data-source="historyData"
        :loading="loading"
        :columns="columns()"
        :show-summary="false"
        :total="historyTotal"
        has-pagination
        :current-page="historyPage"
        :page-size="historyLimit"
        :border-style="true"
        @handleCurrent="handleCurrentChange"
        @handleSize="handlePageSizeChange"
      >
      </base-table-panel>
    </simple-dialog>
  </div>
</template>

<script>
import { handoverWork, historyHandover, recallHandover } from '@api/mainPages';
import moment from 'moment';
export default {
  name: 'workHandover',
  props: {
    workConnectDialog: {
      type: Boolean,
      default: false
    },
    dictsData: {
      type: Array,
      default: () => []
    },
    handoverData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    const handoverPass = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请选择工作交接人'));
      } else {
        callback();
      }
    };
    const startPass = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请选择开始时间'));
      } else if (value.getTime() < Date.parse(new Date())) {
        callback(new Error('开始时间必须大于当前时间'));
      } else {
        callback();
      }
    };
    const endPass = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请选择结束时间'));
      } else if (
        this.ruleForm.startValue &&
        value.getTime() < this.ruleForm.startValue.getTime() + 60000
      ) {
        callback(new Error('结束时间必须大于开始时间1分钟'));
      } else {
        callback();
      }
    };
    const workPass = (rule, value, callback) => {
      if (!value || !value.length) {
        callback(new Error('请选择工作交接内容'));
      } else {
        callback();
      }
    };
    return {
      historyLimit: 10,
      historyPage: 1,
      historyTotal: 0,
      historyData: [],
      loading: false,
      historyConnectDialog: false,
      ruleForm: {
        region: '',
        content: [],
        startValue: new Date(),
        endValue: new Date()
      },
      rules: {
        region: [{ validator: handoverPass, trigger: 'blur' }],
        content: [{ validator: workPass, trigger: 'blur' }],
        startValue: [{ validator: startPass, trigger: 'blur' }],
        endValue: [{ validator: endPass, trigger: 'blur' }]
      },
      columns() {
        const _this = this;
        return [
          {
            prop: 'name',
            label: '发起人',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '140'
          },
          {
            prop: 'handoverUser',
            label: '交接人',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '140'
          },
          {
            prop: 'scenesId',
            label: '交接模块',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '140'
          },
          {
            prop: 'startTime',
            label: '开始时间',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '170'
          },
          {
            prop: 'endTime',
            label: '结束时间',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '170'
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            width: '140',
            actions: [
              {
                code: 'del',
                text: '撤回',
                disabled: _this.publishCheck,
                onClick: _this.historyRecall
              }
            ]
          }
        ];
      }
    };
  },
  methods: {
    // 工作交接 确定
    workConnectSave(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const workStart = JSON.parse(
            JSON.stringify(this.ruleForm.startValue)
          );
          const workStartDate = moment(workStart).format('YYYY-MM-DD');
          const workEnd = JSON.parse(JSON.stringify(this.ruleForm.endValue));
          const workEndDate = moment(workEnd).format('YYYY-MM-DD');
          const params = {
            endDate: workEndDate,
            handoverUser: this.ruleForm.region,
            scenesIds: this.ruleForm.content,
            startDate: workStartDate
          };
          handoverWork(params).then((res) => {
            if (res.data) {
              this.$message.success('工作交接成功！');
              this.workConnectDialog = false;
              this.$refs[formName].resetFields();
            } else {
              this.$message.error('工作交接失败:' + res.error.message);
            }
          });
        } else {
          return false;
        }
      });
    },
    // 工作交接 取消
    workConnectCancel(formName) {
      this.workConnectDialog = false;
      this.$refs[formName].resetFields();
      this.$emit('workHandoverClick', this.workConnectDialog);
    },
    // 历史交接 显示
    historyBtn() {
      this.historyConnectDialog = true;
      this.queryHistory();
    },
    // 历史交接 确定
    historyConnectSave() {
      this.historyConnectDialog = false;
    },
    // 历史交接 取消
    historyConnectCancel() {
      this.historyConnectDialog = false;
    },
    // 查询历史交接列表
    queryHistory() {
      this.loading = true;
      const params = {
        limit: this.historyLimit,
        page: this.historyPage
      };
      historyHandover(params).then((res) => {
        this.loading = false;
        if (res.data) {
          this.historyData = res.data.row;
          this.historyTotal = res.data.total;
        } else {
          this.$message.error('历史交接列表查询失败:' + res.error.message);
        }
      });
    },
    // 历史交接 撤回
    historyRecall(row) {
      recallHandover({ id: row.id }).then(({ error }) => {
        if (!error) {
          this.$message.success('历史交接撤回成功！');
          this.queryHistory();
        } else {
          this.$message.error('历史交接撤回失败:' + error.message);
        }
      });
    },
    // 历史交接 撤回 根据状态判断是否置灰
    publishCheck(record) {
      return record.status !== 0;
    },
    // 历史交接 改变页码
    handleCurrentChange(el) {
      this.historyPage = el;
      this.queryHistory();
    },
    // 历史交接 改变页大小
    handlePageSizeChange(el) {
      this.historyLimit = el;
      this.historyPage = 1;
      this.queryHistory();
    }
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
.workHandover .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 200px;
}
.workHandover .el-select {
  width: 100%;
}
</style>
