




















































































import { Component, Vue } from 'vue-property-decorator';
import workbenchMemo from '../components/workbenchMemo.vue';
import workbenchCalendar from '../components/workbenchCalendar.vue';
import workbenchTableList from '../components/workbenchTableList.vue';
import workHandover from '../components/workHandover.vue';
import businessScene from '../components/businessScene.vue';
import temporaryTask from '../components/temporaryTask.vue';
import addItemScene from '../components/addItemScene.vue';
import productCont from '../components/productCont.vue';
import {
  getScenesForWorkHandover,
  userDicts,
  queryScenesTag,
  queryAllDept
} from '@api/mainPages';

@Component({
  components: {
    workbenchMemo,
    workbenchCalendar,
    workbenchTableList,
    workHandover,
    businessScene,
    temporaryTask,
    addItemScene,
    productCont
  }
})
export default class Index extends Vue {
  // 工作交接 历史交接
  private workConnectDialog = false;
  private dictsData: Array<any> = [];
  private handoverData: Array<any> = [];
  // 临时任务
  private temporaryDialog = false;
  // 新增业务场景
  private addBusinessDialog = false;
  private addScenesTags: Array<any> = [];
  private queryDeptData: Array<any> = [];
  // 新增项目场景
  private itemSenceDialog = false;
  // 产品特性
  private productDialog = false;

  // 产品特性弹窗显示
  productBtn() {
    this.productDialog = true;
  }
  // 产品特性弹窗子组件传过来的数据
  productDia(val) {
    this.productDialog = val;
  }

  // 新增项目场景
  addItemBtn() {
    this.itemSenceDialog = true;
    this.ScenesTag();
    this.queryDept();
    this.getSuccessor();
  }
  // 新增项目场景子组件传过来的数据
  addItemDialog(val) {
    this.itemSenceDialog = val;
  }

  // 临时任务创建
  temporaryBtn() {
    this.temporaryDialog = true;
  }
  // 临时任务创建子组件传过来的数据
  temporaryTaskDialog(val) {
    this.temporaryDialog = val;
  }

  // 新增业务场景 弹窗显示
  addBusinessBtn() {
    this.addBusinessDialog = true;
    this.ScenesTag();
    this.queryDept();
    this.getSuccessor();
  }
  // 新增业务场景弹窗子组件传过来的数据
  businessSceneDialog(val) {
    this.addBusinessDialog = val;
  }
  // 新增业务场景 场景标签
  ScenesTag() {
    queryScenesTag().then((res) => {
      if (res.data) {
        this.addScenesTags = res.data;
      } else {
        this.$message.error('场景标签查询失败：' + res.error.message);
      }
    });
  }
  // 新增业务场景 所属部门
  queryDept() {
    queryAllDept().then((res) => {
      if (res.data) {
        this.queryDeptData = res.data;
      } else {
        this.$message.error('所属部门查询失败：' + res.error.message);
      }
    });
  }

  // 工作交接弹窗显示
  workHandoverBtn() {
    this.workConnectDialog = true;
    this.getHandover();
    this.getSuccessor();
  }
  // 工作交接弹窗子组件传过来的数据
  workHandoverDialog(val) {
    this.workConnectDialog = val;
  }
  // 工作交接 交接人列表
  getSuccessor() {
    userDicts().then((res) => {
      if (res.data) {
        this.dictsData = res.data;
      } else {
        this.$message.error('交接人列表查询失败：' + res.error.message);
      }
    });
  }
  // 工作交接 交接内容列表
  getHandover() {
    getScenesForWorkHandover().then((res) => {
      if (res.data) {
        this.handoverData = res.data;
      } else {
        this.$message.error('交接内容列表查询失败：' + res.error.message);
      }
    });
  }
}
