









































































































































































































































































































































































































import { Component, Vue, Prop, Ref } from 'vue-property-decorator';
import {
  tempTask,
  listTag,
  getUserInfoByCode,
  userDicts
} from '@api/mainPages';
@Component({
  components: {}
})
export default class TemporaryTask extends Vue {
  @Ref() workRuleForm: any;
  @Prop({ default: false }) temporaryDialog: boolean;
  private sponsorWayShow = false;
  private onceWayShow = false;
  private moreHide = true;
  private moreShow = false;
  private telShow = false;
  private emailShow = false;
  private wechatShow = false;
  private temporaryReset = false;
  private changeId = 0;
  private remindId = 1;
  private sponsorId = 0;
  private frequencyId = 0;
  private userNameData: Array<any> = [];
  private taskTagsData: Array<any> = [];
  private skewingOptions: Array<any> = [
    { value: '选项1', label: '超时前' },
    { value: '选项2', label: '超时后' }
  ];
  private frequencyOptions: Array<any> = [
    { value: '选项2', label: '分', id: 0 },
    { value: '选项3', label: '时', id: 1 },
    { value: '选项4', label: '日', id: 2 },
    { value: '选项5', label: '周', id: 3 },
    { value: '选项6', label: '月', id: 4 },
    { value: '选项7', label: '年', id: 5 },
    { value: '选项8', label: '一次性', id: 6 }
  ];
  private options: Array<any> = [
    { value: '选项1', label: '黄金糕' },
    { value: '选项2', label: '双皮奶' },
    { value: '选项3', label: '蚵仔煎' },
    { value: '选项4', label: '龙须面' },
    { value: '选项5', label: '北京烤鸭' }
  ];
  private remindOptions: Array<any> = [
    { value: '选项1', label: '短信', id: 1 },
    { value: '选项2', label: '邮件', id: 2 },
    { value: '选项3', label: '微信', id: 3 },
    { value: '选项4', label: '站内信', id: 4 }
  ];
  private sponsorDateOptions: Array<any> = [
    { value: '选项1', label: '自然日', id: 0 },
    { value: '选项2', label: '工作日', id: 1 }
  ];
  private taskFrequencyOptions: Array<any> = [
    { value: '选项1', label: '单次任务', id: 0 },
    { value: '选项2', label: '周期任务', id: 1 }
  ];
  private timeoutSetOptions: Array<any> = [
    { value: '选项1', label: '1小时', id: 0 },
    { value: '选项2', label: '5小时', id: 1 },
    { value: '选项3', label: '24小时', id: 2 },
    { value: '选项4', label: '48小时', id: 3 }
  ];
  private launchWayOptions: Array<any> = [
    { value: '选项1', label: '手工发起', id: 0 },
    { value: '选项2', label: '定时发起', id: 1 },
    { value: '选项3', label: '外部发起', id: 2 }
  ];
  private ruleForm: any = {
    temporaryName: '', // 任务名称 string类型
    temporaryDesc: '', // 任务描述 string类型
    checkPossess: false, // 自有
    checkMonitor: false, // 关键
    taskFrequency: '', // 任务频率
    sponsorWayNum: 1, // 任务超时时间 int类型
    temporaryStartTime: new Date(), // 任务开始时间 string类型
    temporaryEndTime: new Date(),
    temporaryHandler: '', // 任务办理人 string类型
    temporaryResource: '0', // 关联产品 int类型
    temporaryTally: '', // 任务标签 string类型
    temporaryCont: '', // 消息内容 string类型
    temporaryType: '', // 提醒类型 string类型
    frequencyNum: '0', // 提醒频率 string类型
    temporaryTime: 0, // 任务提醒 频率单位
    timeoutDay: 0, //单次任务任务发起 偏移单位 int类型
    timeoutHour: 0,
    launchWay: '',
    phoneNumber: '',
    addressee: '',
    copyTo: '',
    wechat: '',
    sponsorStartTime: new Date(),
    sponsorEndTime: new Date(),
    sponsorWayDate: 0, // 周期任务发起方式 偏移单位
    temporarySkewingTime: 0, // 任务提醒 偏移单位
    skewingTime: '',
    temporaryNum: 0, // 偏移天数 int类型
    skewingNum: 0,
    temporaryTitle: '' // 消息标题
  };
  private rules: any = {
    temporaryName: [
      { required: true, message: '请输入任务名称', trigger: 'blur' }
    ]
  };
  // 更多···
  moreShowBtn() {
    this.moreHide = false;
    this.moreShow = true;
    this.taskTagsList();
    this.itemSceneUser();
  }
  // 任务标签
  taskTagsList() {
    listTag().then((res) => {
      if (res.data) {
        this.taskTagsData = res.data;
      }
    });
  }
  // 临时任务 办理人
  itemSceneUser() {
    const userCode = this.$store.state.user.userInfo.usercode;
    const userCodeName = userCode.split(',');
    getUserInfoByCode(userCodeName).then((res) => {
      if (res.data) {
        this.userName = res.data.rows[0].deptId;
        userDicts({ deptId: this.userName }).then((res) => {
          if (res.data) {
            this.userNameData = res.data;
          } else {
            this.$message.error('办理人列表查询失败：' + res.error.message);
          }
        });
      }
    });
  }
  // 临时任务保存
  temporaryTaskSave() {
    const params = {
      cc: this.ruleForm.copyTo, // 抄送人 string类型
      message: this.ruleForm.temporaryCont, // 消息内容 string类型
      messageTitle: this.ruleForm.temporaryTitle, // 消息标题 string类型
      offsetUnit: this.sponsorId, // 偏移单位 int类型
      outTime: this.ruleForm.sponsorWayNum, // 任务超时时间 int类型
      outTimeType: this.sponsorId, // 任务超时类型 int类型
      productCode: '', // 产品代码 string类型
      productName: '', // 产品名称 string类型
      monitorTask: Number(this.ruleForm.checkMonitor), // 关键任务 int类型
      possessTask: Number(this.ruleForm.checkPossess), // 自有任务 int
      recipient: this.ruleForm.phoneNumber, // 接收人 string类型
      relatedProduct: parseInt(this.ruleForm.temporaryResource), // 关联产品 int类型
      remindDay: this.ruleForm.temporaryNum, // 偏移天数 int类型
      remindFreq: this.ruleForm.frequencyNum, // 提醒频率 string类型
      remindType: this.remindId, // 提醒类型 int类型
      remindUnit: this.frequencyId, // 消息频率单位 int类型
      taskEndTime: '',
      taskFreq: this.changeId, // 任务频率 int类型
      taskHandler: this.ruleForm.temporaryHandler, // 任务办理人 string类型
      taskName: this.ruleForm.temporaryName, // 任务名称 string类型
      taskNote: this.ruleForm.temporaryDesc, // 任务描述 string类型
      taskOffsetUnit: 0, // 任务偏移单位 int类型
      taskRemindDay: 0, // 任务偏移天数 int类型
      taskRemindFreq: 0, // 任务提醒频率 int类型
      taskRemindUnit: 0, // 任务频率单位 int类型
      taskStartTime: this.ruleForm.temporaryStartTime, // 任务开始时间 string类型
      taskTag: this.ruleForm.temporaryTally // 任务标签 string类型 没有值
    };
    this.workRuleForm.validate((valid) => {
      if (valid) {
        tempTask(params).then((res) => {
          if (res.data) {
            this.$message.success('临时任务创建成功！');
            this.temporaryDialog = false;
            this.workRuleForm.resetFields();
            this.ruleForm.checkPossess = false;
            this.ruleForm.checkMonitor = false;
            this.telShow = false;
            this.emailShow = false;
            this.wechatShow = false;
            this.$emit('temporaryTaskClick', this.temporaryDialog);
          } else {
            this.$message.error('临时任务创建失败:' + res.error.message);
          }
        });
      }
    });
  }
  // 临时任务保存并创建下一条
  temporarySaveNext() {
    this.temporaryReset = !this.temporaryReset;
    const params = {
      cc: this.ruleForm.copyTo, // 抄送人 string类型
      message: this.ruleForm.temporaryCont, // 消息内容 string类型
      messageTitle: this.ruleForm.temporaryTitle, // 消息标题 string类型
      offsetUnit: this.sponsorId, // 偏移单位 int类型
      outTime: this.ruleForm.sponsorWayNum, // 任务超时时间 int类型
      outTimeType: this.sponsorId, // 任务超时类型 int类型
      productCode: '', // 产品代码 string类型
      productName: '', // 产品名称 string类型
      recipient: this.ruleForm.phoneNumber, // 接收人 string类型
      relatedProduct: parseInt(this.ruleForm.temporaryResource), // 关联产品 int类型
      remindDay: this.ruleForm.temporaryNum, // 偏移天数 int类型
      remindFreq: this.ruleForm.frequencyNum, // 提醒频率 string类型
      remindType: this.remindId, // 提醒类型 int类型
      remindUnit: this.frequencyId, // 消息频率单位 int类型
      taskEndTime: '', // 任务结束时间 string类型
      taskFreq: this.changeId, // 任务频率 int类型
      taskHandler: this.ruleForm.temporaryHandler, // 任务办理人 string类型
      taskName: this.ruleForm.temporaryName, // 任务名称 string类型
      taskNote: this.ruleForm.temporaryDesc, // 任务描述 string类型
      taskOffsetUnit: 0, // 任务偏移单位 int类型
      taskRemindDay: 0, // 任务偏移天数 int类型
      taskRemindFreq: 0, // 任务提醒频率 int类型
      taskRemindUnit: 0, // 任务频率单位 int类型
      taskStartTime: this.ruleForm.temporaryStartTime, // 任务开始时间 string类型
      taskTag: this.ruleForm.temporaryTally // 任务标签 string类型 没有值
    };
    this.workRuleForm.validate((valid) => {
      if (valid) {
        tempTask(params).then((res) => {
          if (res.data) {
            this.$message.success('临时任务保存成功并创建下一条');
            this.$emit('temporaryTaskClick', this.temporaryDialog);
          } else {
            this.$message.error('临时任务创建失败:' + res.error.message);
          }
        });
      }
    });
  }
  // 临时任务创建 重置
  temporaryResetBtn() {
    this.workRuleForm.resetFields();
    this.ruleForm.checkPossess = false;
    this.ruleForm.checkMonitor = false;
    this.telShow = false;
    this.emailShow = false;
    this.wechatShow = false;
  }
  // 临时任务创建小窗口取消
  temporaryTaskCancel() {
    this.temporaryDialog = false;
    this.workRuleForm.resetFields();
    this.ruleForm.checkPossess = false;
    this.ruleForm.checkMonitor = false;
    this.telShow = false;
    this.emailShow = false;
    this.wechatShow = false;
    this.$emit('temporaryTaskClick', this.temporaryDialog);
  }
  // 临时任务创建 任务频率选中值改变  单次任务
  taskFrequencyChange(item) {
    this.changeId = item.id;
    if (item.id === 1) {
      this.sponsorWayShow = true;
      this.onceWayShow = false;
    } else {
      this.sponsorWayShow = false;
      this.onceWayShow = true;
    }
  }
  // 临时任务创建 提醒类型选中值改变  手机号码
  remindChange(item) {
    this.remindId = item.id;
    if (item.id === 1) {
      this.telShow = true;
      this.emailShow = false;
      this.wechatShow = false;
    } else if (item.id === 2) {
      this.telShow = false;
      this.emailShow = true;
      this.wechatShow = false;
    } else if (item.id === 3) {
      this.telShow = false;
      this.emailShow = false;
      this.wechatShow = true;
    } else {
      this.telShow = false;
      this.emailShow = false;
      this.wechatShow = false;
    }
  }
  // 提醒频率单位选中值改变 时分秒
  frequencyChange(val) {
    this.frequencyId = val;
  }
  // 单次任务，周期任务,偏移天数    工作日
  sponsorChange(val) {
    this.sponsorId = val.id;
  }
}
