<template>
  <!-- 日历 -->
  <div class="footCalendar">
    <div class="calendarTitle">
      <span>节假日</span>
      <i class="el-icon-setting" style="margin: 5px 10px 0 0;"></i>
      <el-select
        v-model="exchangeValue"
        placeholder="请选择"
        style="width: 40%;"
      >
        <el-option
          @click.native="marketChange(item)"
          v-for="item in marketData"
          :key="item.id"
          :label="item.marketName"
          :value="item.marketCode"
        >
        </el-option>
      </el-select>
    </div>
    <div v-loading="loading" class="left-calendar">
      <FullCalendar
        ref="calendar"
        default-view="dayGridMonth"
        :height="340"
        :header="header"
        :button-text="buttonText"
        :plugins="calendarPlugins"
        :day-render="dayRender"
        :views="views"
        weekNumberCalculation="ISO"
        :fixed-week-count="false"
        :events="events"
      />
    </div>
  </div>
</template>

<script>
import { getCalendarExport, getMarketList } from '@api/mainPages';
import FullCalendar from '@fullcalendar/vue';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import momentPlugin from '@fullcalendar/moment';
const weekdayList = ['日', '一', '二', '三', '四', '五', '六'];
export default {
  name: 'workbenchCalendar',
  components: {
    FullCalendar
  },
  data() {
    return {
      header: {
        left: '',
        center: 'title',
        right: 'today prev next'
      },
      buttonText: {
        today: '今天',
        month: '月',
        week: '周',
        day: '日'
      },
      calendarPlugins: [dayGridPlugin, interactionPlugin, momentPlugin],
      views: {
        dayGridMonth: {
          titleFormat: 'YYYY[年]MM[月]',
          columnHeaderText: (date) => weekdayList[date.getDay()]
        }
      },
      events: (info, callback) => {
        this.calendarStart = JSON.parse(JSON.stringify(info.start));
        this.calendarEnd = JSON.parse(JSON.stringify(info.end));
        this.queryCalendar();
      },
      // 以上是 Fullcalendar 插件所需配置
      calendarStart: '',
      calendarEnd: '',
      dayInfo: [],
      loading: false,
      exchangeValue: 'china',
      marketData: []
    };
  },
  methods: {
    // 查询交易市场列表
    queryMarket() {
      getMarketList().then((res) => {
        if (res.data) {
          this.marketData = res.data;
        } else {
          this.$message.error('交易市场列表查询失败：' + res.error.message);
        }
      });
    },
    // 改变交易市场
    marketChange(item) {
      this.exchangeValue = item.marketCode;
      this.queryCalendar();
    },
    // 查询节假日列表
    queryCalendar() {
      // this.calendarEnd = moment(this.calendarEnd).format('YYYY-MM-DD');
      // this.calendarStart = moment(this.calendarStart).format('YYYY-MM-DD');
      const params = {
        endDate: this.calendarEnd,
        market: this.exchangeValue,
        startDate: this.calendarStart
      };
      this.loading = true;
      getCalendarExport(params).then((res) => {
        this.loading = false;
        if (res.data) {
          this.dayInfo = res.data;
        } else {
          this.$message.error('查询节假日列表失败:' + res.error.message);
        }
      });
    },
    // 通过此回调，可以修改每个日历视图中的日单元格元素。
    dayRender(info) {
      setTimeout(() => {
        if (this.dayInfo.length) {
          this.dayInfo.map((item) => {
            // const itemDate = new Date(item.holidayDate + ' 00:00:00');
            const itemDate = new Date(item.holidayDate);
            if (itemDate.getTime() === info.date.getTime()) {
              if (item.holidayType === 1 || item.holidayType === 0) {
                info.el.innerHTML = `<span class="dayOff">休</span>`;
              }
            }
          });
        }
      }, 600);
    }
  },
  mounted() {
    this.queryMarket();
  }
};
</script>

<style lang="scss">
@import '~@fullcalendar/core/main.css';
@import '~@fullcalendar/daygrid/main.css';

.footCalendar {
  /* 搜索框 */
  .el-input--small .el-input__inner {
    height: 28px;
    line-height: 28px;
  }
  .dayOff {
    width: 14px;
    height: 14px;
    line-height: 14px;
    font-size: 11px;
    text-align: center;
    /* display: inline-block; */
    color: #fff;
    float: right;
    font-weight: 400;
    background-color: #ff5741;
  }
  /* 日历标题 */
  .fc-ltr .fc-dayGrid-view .fc-day-top .fc-day-number {
    float: left;
  }
  .left-calendar {
    .fc-toolbar.fc-header-toolbar {
      height: 30px;
      margin: 10px 20px;
      .fc-right {
        height: 30px;
        display: flex;
        .fc-button {
          line-height: 0.5;
          .fc-icon {
            font-size: 12px;
          }
        }
        .fc-button-primary:not(:disabled):active,
        .fc-button-primary:not(:disabled).fc-button-active {
          background-color: rgba(47, 106, 255, 1);
          border-color: rgba(47, 106, 255, 1);
        }
        .fc-button-primary {
          background-color: rgba(47, 106, 255, 1);
          border-color: rgba(47, 106, 255, 1);
        }
      }
      .fc-center {
        h2 {
          font-size: 18px;
          line-height: 30px;
        }
      }
    }
    .fc-view-container {
      margin: 0 20px;
      .fc-dayGrid-view .fc-body .fc-row {
        min-height: 44px;
      }
    }
  }
}
</style>
<style lang="scss" scoped>
.footCalendar {
  height: auto;
  background-color: #fff;
}
.calendarTitle {
  display: flex;
  padding: 7px 20px;
  background-color: #f4f5f6;
}
.calendarTitle span {
  width: 50px;
  font-size: 14px;
  padding-top: 5px;
  font-size: 14px;
}
</style>
