<template>
  <!-- 穿梭框 -->
  <div class="transfer" style="text-align: center">
    <el-transfer
      style="text-align: left; display: inline-block"
      v-model="value4"
      filterable
      :titles="['未选', '已选']"
      :button-texts="[]"
      :format="{
        hasChecked: '${checked}/${total}',
        hasChecked: '${checked}/${total}'
      }"
      @change="handleChange"
      :data="data"
    >
      <div slot-scope="{ option }">
        <span
          style="display: flex;justify-content: space-between;width: 127px;"
        >
          <span>{{ option.label }}</span>
          <input
            type="checkbox"
            @click="CheckBtn(option)"
            v-model="checkState"
            style="margin-top: 8px;"
            v-show="option.checkBoxShow"
          />
        </span>
      </div>
    </el-transfer>
  </div>
</template>

<script>
export default {
  name: 'TableColumnEdit',
  data() {
    const generateData = (_) => {
      const data = [];
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: `备选项 ${i}`,
          disabled: i % 4 === 0,
          checkState: false,
          checkBoxShow: false
        });
      }
      return data;
    };
    return {
      data: generateData(),
      value4: [1]
    };
  },
  methods: {
    CheckBtn(option) {
      this.checkState = !this.checkState;
    },
    handleChange(value, direction, movedKeys) {
      if (direction == 'right') {
        this.checkBoxShow = true;
        this.checkState = false;
      }
      if (direction == 'left') {
        this.checkBoxShow = false;
        this.checkState = false;
      }
      for (const item in this.data) {
        if (this.value4.indexOf(this.data[item].key) > -1) {
          this.data[item].checkBoxShow = true;
        } else {
          this.data[item].checkBoxShow = false;
        }
      }
    }
  },
  created() {
    if (this.value4.length !== 0 && this.value4 !== []) {
      this.checkBoxShow = true;
      for (const item in this.data) {
        if (this.value4.indexOf(this.data[item].key) > -1) {
          this.data[item].checkBoxShow = true;
        }
      }
    } else {
      this.checkBoxShow = false;
    }
  }
};
</script>

<style lang="scss" scoped></style>
