  // 新增项目场景创建单选
  radioChange() {
    if (this.ruleForm.manner === '批量导入') {
      this.singleShow = false;
      this.addSinhleShow = false;
      this.sinhleShow = true;
    } else if (this.ruleForm.manner === '单条添加') {
      this.singleShow = true;
      this.addSinhleShow = true;
      this.sinhleShow = false;
    }
  }
  // 新增项目场景 下一步
  abbTaskBtn() {
    this.addItem = false;
    this.addTask = true;
    this.addItemTitle = !this.addItemTitle;
  }
  // 新增项目场景 上一步
  abbTaskReturn() {
    this.addItem = true;
    this.addTask = false;
    this.addItemTitle = !this.addItemTitle;
  }
  // 更多···
  moreShowBtn() {
    this.moreHide = false;
    this.moreShow = true;
    this.taskTagsList();
    this.itemSceneUser();
  }
  // 单次任务，周期任务,偏移天数 超时单位  工作日
  sponsorChange(val) {
    this.sponsorId = val;
  }
  // 任务频率选中值改变
  taskFrequencyChange(item) {
    this.changeId = item.id;
    if (item.id === 1) {
      this.sponsorWayShow = true;
      this.onceWayShow = false;
    } else {
      this.sponsorWayShow = false;
      this.onceWayShow = true;
    }
  }
  // 提醒频率选中值改变 时分秒
  frequencyChange(val) {
    this.frequencyId = val;
  }
  // 临时任务创建 提醒类型选中值改变  手机号码
  remindChange(item) {
    this.remindId = item.id;
    if (item.id === 1) {
      this.telShow = true;
      this.emailShow = false;
      this.wechatShow = false;
    } else if (item.id === 2) {
      this.telShow = false;
      this.emailShow = true;
      this.wechatShow = false;
    } else if (item.id === 3) {
      this.telShow = false;
      this.emailShow = false;
      this.wechatShow = true;
    } else {
      this.telShow = false;
      this.emailShow = false;
      this.wechatShow = false;
    }
  }
  // 任务标签
  taskTagChange(val) {
    this.taskTagId = val;
  }
  // 任务标签下拉列表
  taskTagsList() {
    listTag().then((res) => {
      if (res.data) {
        this.taskTagsData = res.data;
      }
    });
  }
  // 临时任务 办理人
  itemSceneUser() {
    const userCode = this.$store.state.user.userInfo.usercode;
    const userCodeName = userCode.split(',');
    getUserInfoByCode(userCodeName).then((res) => {
      if (res.data) {
        this.userName = res.data.rows[0].deptId;
        userDicts({ deptId: this.userName }).then((res) => {
          if (res.data) {
            this.userNameData = res.data;
          } else {
            this.$message.error('办理人列表查询失败：' + res.error.message);
          }
        });
      }
    });
  }
