























































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
  components: {}
})
export default class ProductCont extends Vue {
  @Prop({ default: false }) productDialog: boolean;
  @Prop({ default: [] }) readonly sceneReturnData: Array<any>;
  private form = {
    name: '',
    productValue: '',
    person: ''
  };

  // 确定
  productSave() {
    // const menuForm: any = this.$refs['workRuleForm'];
    //   menuForm.validate((valid: any) => {
    //     if (valid) {}
  }
  // 取消
  productCancel() {
    this.productDialog = false;
    this.form = {
      name: '',
      productValue: '',
      person: ''
    };
    this.$emit('productClick', this.productDialog);
  }
  mounted() {}
}
