



























































import { Component, Vue } from 'vue-property-decorator';

import {
  queryMemo,
  newAddMemo,
  deleteMemo,
  finishMemoStatus
} from '@api/mainPages';
import moment from 'moment';
import cloneDeep from 'lodash/cloneDeep';

@Component({
  components: {}
})
export default class WorkbenchMemo extends Vue {
  private loading = false;
  private contentTishi = '新增';
  private isWancheng = '全部完成';
  private momeTableData: Array<any> = [];
  private memoDateList: Array<any> = [new Date(), new Date()];

  // 备忘录区间段改变
  memoQuantumChange(val) {
    if (val) {
      this.getmemo(val);
    }
  }
  // 获取备忘录列表
  getmemo(value) {
    this.memoDateList = JSON.parse(JSON.stringify(value));
    const memoStartDate = moment(this.memoDateList[0]).format('YYYY-MM-DD');
    const memoEndDate = moment(this.memoDateList[1]).format('YYYY-MM-DD');
    const params = {
      userId: 'xiatian',
      startDate: memoStartDate,
      endDate: memoEndDate
    };
    this.loading = true;
    queryMemo(params).then((res) => {
      this.loading = false;
      if (res.data) {
        this.momeTableData = res.data;
        const hasCheck = this.momeTableData.filter((item) => {
          return item.eventStatus === '1';
        });
        this.toggleSelection(hasCheck);
      } else {
        this.$message.error('备忘录列表查询失败：' + res.error.message);
      }
    });
  }
  // 新增备忘录不能为空
  addRecords() {
    if (this.momeTableData[0] && this.momeTableData[0].title === '') {
      this.$message({
        message: '备忘录信息不能为空',
        type: 'warning'
      });
    } else {
      let addMemoDate = new Date();
      addMemoDate = JSON.parse(JSON.stringify(addMemoDate));
      const newMemoDate = moment(addMemoDate).format('YYYY-MM-DD');
      this.momeTableData.unshift({
        title: '',
        isEdit: true,
        date: newMemoDate
      });
    }
  }
  // 删除空字段
  deleteEmptyParam(params) {
    for (const key in params) {
      if (
        params[key] === '' ||
        params[key] === undefined ||
        params[key] === null
      ) {
        delete params[key];
      }
    }
  }
  // 修改新增备忘录提示
  cellClick(row) {
    if (row.id) {
      this.contentTishi = '修改';
    } else {
      this.contentTishi = '新增';
    }
    row.isEdit = true;
    this.momeTableData.forEach((item, index) => {
      if (item.id === row.id) {
        this.momeTableData.splice(index, 1, row);
      }
    });
  }
  // 新增、修改备忘录
  changeRecord(row) {
    row.isEdit = false;
    this.deleteEmptyParam(row);
    if (row.id != '' && row.id != undefined) {
      const params = {
        id: row.id,
        date: row.natureStrDay,
        title: row.title
      };
      newAddMemo(params).then(({ error }) => {
        if (!error) {
          this.$message({
            message: `${this.contentTishi}备忘录成功!`,
            type: 'success'
          });
          this.getmemo(row);
        } else {
          this.$message({
            message: `${this.contentTishi}备忘录失败：${error.message}`,
            type: 'error'
          });
        }
      });
    } else {
      const params = {
        id: '',
        date: row.date,
        title: row.title
      };
      newAddMemo(params).then(({ error }) => {
        if (!error) {
          this.$message({
            message: `${this.contentTishi}备忘录成功!`,
            type: 'success'
          });
          this.getmemo(row);
        } else {
          this.$message({
            message: `${this.contentTishi}备忘录失败：${error.message}`,
            type: 'error'
          });
        }
      });
    }
  }
  // 删除备忘录
  delRow(row) {
    if (row.id) {
      deleteMemo({ id: row.id }).then(({ error }) => {
        if (!error) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.getmemo(row);
        } else {
          this.$message({
            message: `删除失败：${error.message}`,
            type: 'error'
          });
        }
      });
    } else {
      this.momeTableData.shift();
    }
  }
  // 默认完成状态
  toggleSelection(rows) {
    const multipleTable: any = this.$refs.multipleTable;
    if (rows) {
      rows.forEach((row) => {
        multipleTable.toggleRowSelection(row, true);
      });
    } else {
      multipleTable.clearSelection();
    }
  }
  // 全选
  handleSelectAll(status) {
    if (status.length > 0 && status.length === this.momeTableData.length) {
      this.isWancheng = '取消完成';
    } else if (
      status.length > 0 &&
      status.length !== this.momeTableData.length
    ) {
      this.isWancheng = '全部完成';
    } else {
      this.isWancheng = '全部完成';
    }
    let copyStatus = cloneDeep(status);
    if (copyStatus.length !== 0) {
      const startIndex = this.momeTableData.filter(
        (copyItem) => copyItem.eventStatus === '0'
      );
      copyStatus = startIndex;
      this.momeTableData.forEach((item) => {
        item.eventStatus = '1';
      });
    } else {
      copyStatus = this.momeTableData;
      this.momeTableData.forEach((item) => {
        item.eventStatus = '0';
      });
    }
    const arr: Array<any> = [];
    const arrDate: Array<any> = [];
    copyStatus.forEach((item) => {
      arr.push(item.id);
      arrDate.push(item.natureStrDay);
    });

    const params = { ids: arr, date: arrDate };
    finishMemoStatus(params).then(({ error }) => {
      if (error) {
        this.$message.error('修改备忘录状态失败！');
      } else {
        this.$message.success('修改备忘录状态修改成功！');
      }
    });
  }
  // 单行
  handleSelectionChange(status, row) {
    if (status.length > 0 && status.length === this.momeTableData.length) {
      this.isWancheng = '取消完成';
    } else if (
      status.length > 0 &&
      status.length !== this.momeTableData.length
    ) {
      this.isWancheng = '全部完成';
    } else {
      this.isWancheng = '全部完成';
    }
    row.eventStatus = row.eventStatus == '0' ? '1' : '0';
    const params = { ids: row.id, date: row.natureStrDay };
    finishMemoStatus(params).then(({ error }) => {
      if (error) {
        this.$message.error('修改备忘录状态失败！');
      } else {
        this.$message.success('修改备忘录状态修改成功！');
      }
    });
  }
  mounted() {
    this.getmemo(this.memoDateList);
  }
}
