  ];
  private dingyueList: Array<any> = [
    { name: '详情', action: 'taskDetails' },
    { name: '传阅', action: 'taskCircularize' },
    { name: '强制接管', action: 'taskTakeOver' }
  ];
  private priorityStateData: Array<any> = [
    { text: '低级', value: 0 },
    { text: '中级', value: 1 },
    { text: '高级', value: 2 }
  ];
  private operationWayData: Array<any> = [
    { text: 'null', value: 0 },
    { text: '检查项执行中', value: 1 },
    { text: 'RPA执行中', value: 2 },
    { text: 'OCR执行中', value: 3 },
    { text: '待人工处理', value: 4 },
    { text: '获取数据中', value: 5 }
  ];
  private taskStateData: Array<any> = [
    { text: '未完成', value: 0 },
    { text: '催办中', value: 1 },
    { text: '超时', value: 2 },
    { text: '已完成', value: 3 },
    { text: '已结束', value: 4 }
  ];
  private taskMsg: any = {};
  taskStateFormatter(row: object, column: object, cellValue: number) {
    if (arrayFind(this.taskStateData, 'value', cellValue)) {
      return arrayFind(this.taskStateData, 'value', cellValue).text;
    } else {
      return '';
    }
  }
  operationWayFormatter(row: object, column: object, cellValue: number) {
    if (arrayFind(this.operationWayData, 'value', cellValue)) {
      return arrayFind(this.operationWayData, 'value', cellValue).text;
    } else {
      return '';
    }
  }
  columnsList() {
    const _this = this;
    return [
      {
        prop: '',
        label: '序号',
        type: 'selection',
        align: 'center',
        showOverflowTooltip: true,
        'min-width': '60'
      },
      {
        prop: 'taskName',
        label: '任务名称',
        'header-align': 'left',
        minWidth: 130,
        sortable: true,
        'show-overflow-tooltip': true
      },
      {
        prop: 'runType',
        label: '运行方式',
        minWidth: 110,
        'header-align': 'left',
        sortable: true,
        'show-overflow-tooltip': true,
        formatter: _this.operationWayFormatter
      },
      {
        prop: 'taskStartTime',
        label: '任务开始时间',
        'header-align': 'left',
        minWidth: 160,
        sortable: true,
        'show-overflow-tooltip': true
      },
      {
        prop: 'taskEndTime',
        label: '任务截止时间',
        'header-align': 'left',
        minWidth: 160,
        sortable: true,
        'show-overflow-tooltip': true
      },
      {
        prop: 'taskStatus',
        label: '任务状态',
        minWidth: 110,
        'header-align': 'left',
        showOverflowTooltip: true,
        sortable: true,
        formatter: _this.taskStateFormatter
      },
      {
        prop: 'taskPriority',
        'header-align': 'left',
        label: '优先级',
        'min-width': '150',
        editer: {
          type: 'select',
          key: 'taskPriority',
          config: {
            props: {
              placeholder: '请设置优先级',
              clearable: false
            },
            listeners: {
              change: _this.handleStatusChange
            },
            optionConfig: { label: 'text' },
            options: () => _this.priorityStateData
          }
        }
      },
      {
        prop: 'creator',
        label: '发起人',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '130'
      },
      {
        prop: 'stationUser',
        label: '办理人',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '130'
      },
      {
        prop: 'taskDescription',
        label: '备注',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '130'
      },
      {
        label: '操作',
        minWidth: 200,
        align: 'center',
        fixed: 'right',
        render: (h: any, { record }: any) => {
          if (_this.queryParams.taskType === 1) {
            record.actionList = _this.daibanList;
          } else if (_this.queryParams.taskType === 2) {
            record.actionList = _this.yibanList;
          } else if (_this.queryParams.taskType === 3) {
            record.actionList = _this.daiyueList;
          } else if (_this.queryParams.taskType === 4) {
            record.actionList = _this.faqiList;
          } else if (_this.queryParams.taskType === 5) {
            record.actionList = _this.dingyueList;
          } else {
            if (record.taskType === 1) {
              record.actionList = _this.daibanList;
            } else if (record.taskType === 2) {
              record.actionList = _this.yibanList;
            } else if (record.taskType === 3) {
              record.actionList = _this.daiyueList;
            } else if (record.taskType === 4) {
              record.actionList = _this.faqiList;
            }
            // else if (record.taskType === 5) {
            //   record.actionList = _this.dingyueList;
            // }
          }

          const action: any =
            record.actionList.length > 2 ? record.actionList.slice(2) : [];
          return h('div', {}, [
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                style: {
                  display: record.actionList ? '' : 'none'
                },
                on: {
                  click: () => {
                    for (const k in record) {
                      _this.$set(_this.taskMsg, k, record[k]);
                    }
                    record.actionList[0]
                      ? _this[record.actionList[0].action]()
                      : '';
                  }
                }
              },
              record.actionList[0] ? record.actionList[0].name : ''
            ),
            h(
              'span',
              {
                style: {
                  padding: '0 6px',
                  display: record.actionList[1] ? '' : 'none'
                }
              },
              '|'
            ),
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                style: {
                  display: record.actionList[1] ? '' : 'none'
                },
                on: {
                  click: () => {
                    for (const k in record) {
                      _this.$set(_this.taskMsg, k, record[k]);
                    }
                    record.actionList[1]
                      ? _this[record.actionList[1].action]()
                      : '';
                  }
                }
              },
              record.actionList[1] ? record.actionList[1].name : ''
            ),
            h(
              'span',
              {
                style: {
                  padding: '0 6px',
                  display: action.length > 0 ? '' : 'none'
                }
              },
              '|'
            ),
            h(
              'el-dropdown',
              {
                on: {
                  command: _this.handleCommand.bind(_this, record)
                },
                props: {
                  trigger: 'click'
                },
                style: {
                  display: action.length > 0 ? '' : 'none'
                }
              },
              [
                h(
                  'el-button',
                  {
                    props: {
                      type: 'text'
                    }
                  },
                  [
                    '更多',
                    h('i', {
                      class: ['icon', 'el-icon-caret-bottom', 'themeIcon']
                    })
                  ]
                ),
                h(
                  'el-dropdown-menu',
                  {
                    slot: 'dropdown'
                  },
                  action.map((item: any) => {
                    return h(
                      'el-dropdown-item',
                      {
                        props: {
                          command: item.action
                        }
                      },
                      item.name
                    );
                  })
                )
              ]
            )
          ]);
        }
      }
    ];
  }
  // 任务中心 搜索
  private taskShowAll = false;
  private sceneShowAll = false;
  private isQueryhide = true;
  private isQueryShow = false;
  private taskCenterName = '';
  private isQueryTagShow = false;
  private restaurants: Array<any> = [];
  private taskTagList: Array<any> = [];
  private taskTagsList: Array<any> = [];
  private sceneTagsList: Array<any> = [];
  private sceneTagList: Array<any> = [];
  private checkedRowId: Array<any> = [];
  private dateValue: Array<any> = [];
  private queryParams: any = {
    customColumns: '',
    queryEndTime: '',
    queryStartTime: '',
    sceneIds: [],
    tags: [],
    taskTimeZone: 2,
    taskType: 0
  };
  // 传阅
  private circularizeVal = '';
  private circularizeDialog = false;
  private userInfoData: Array<any> = [];
  // 转办
  private manageVal = '';
  private manageDialog = false;
  // 上传文件
  private importLoading = false;
  private importDialogVisible = false;
  private importFiles: Array<any> = [];
  // 修改到期时间
  private amendEndTime = '';
  private amendDialog = false;
  private pickerOptions: any = {
    disabledDate(time) {
      return time.getTime() < Date.now();
    }
  };
  // 批量传阅
  private batchCirculateVal = '';
  private batchCirculateDialog = false;
  // 批量转办
  private taskBatchVal = '';
  private taskBatchDialog = false;
  // 批量优先级设置
  private priorityRadio = 0;
  private prioritySetDialog = false;
  // 场景撤回
  private queryVal = '';
  private sceneRetuenDialog = false;
  private findByScenesData: Array<any> = [];
  sceneColumns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '60'
      },
      {
        prop: 'scenesName',
        label: '场景名称',
        align: 'center',
        showOverflowTooltip: true,
        'min-width': '140'
      },
      {
        prop: 'createTime',
        label: '历次场景发起时间',
        align: 'center',
        showOverflowTooltip: true,
        'min-width': '170'
      },
      {
        prop: '',
        label: '操作',
        align: 'center',
        width: '140',
        actions: [
          {
            code: 'del',
            text: '撤回',
            // disabled: _this.publishCheck,
            onClick: _this.sceneRecall
          }
        ]
      }
    ];
  }
  // 手工发起 临时任务
  private taskValue = '';
  private startTaskDialog = false;
  private taskIdsArr: Array<any> = [];
  private startTaskData: Array<any> = [];
  // 手工发起 场景
  private taskId = '';
  private lanunchId = '';
  private selectScene = '';
  private sceneClassify = '';
  private sceneValueName = '';
  private sceneClassifyId = '';
  private sceneClassifyName = '';
  private startSceneDialog = false;
  private taskLaunchData: Array<any> = [];
  private ScenesByTypeUser: Array<any> = [];
  private sceneClassifyOptions: Array<any> = [
    { value: '选项1', label: '业务场景', id: '1' },
    { value: '选项2', label: '项目场景', id: '2' }
  ];
  // 自定义列
  private businessValue = '';
  private customSetDialog = false;
  private options: Array<any> = [
    { value: '选项1', label: '黄金糕' },
    { value: '选项2', label: '双皮奶' },
    { value: '选项3', label: '蚵仔煎' },
    { value: '选项4', label: '龙须面' },
    { value: '选项5', label: '北京烤鸭' }
  ];

  // 场景标签
  get sceneShowList() {
    if (this.sceneShowAll == false) {
      let sceneShowList: Array<any> = [];
      if (this.sceneTagList.length > 7) {
        for (let i = 0; i < 7; i++) {
          sceneShowList.push(this.sceneTagList[i]);
        }
      } else {
        sceneShowList = this.sceneTagList;
      }
      return sceneShowList;
    } else {
      return this.sceneTagList;
    }
  }
  get sceneText() {
    if (this.sceneShowAll == false) {
      return '展开更多';
    } else {
      return '收起';
    }
  }

  // 任务标签
  get taskShowList() {
    if (this.taskShowAll == false) {
      let taskShowList: Array<any> = [];
      if (this.taskTagList.length > 7) {
        for (let i = 0; i < 7; i++) {
          taskShowList.push(this.taskTagList[i]);
        }
      } else {
        taskShowList = this.taskTagList;
      }
      return taskShowList;
    } else {
      return this.taskTagList;
    }
  }
  get taskText() {
    if (this.taskShowAll == false) {
      return '展开更多';
    } else {
      return '收起';
    }
  }

  // 磁贴展示
  getMagnet() {
    const params = {
      limit: this.limit,
      page: this.page,
      queryEndTime: this.dateValue[1],
      queryStartTime: this.dateValue[0],
      sceneIds: this.queryParams.sceneIds,
      tags: this.queryParams.tags,
      customColumns: this.queryParams.customColumns,
      taskTimeZone: this.queryParams.taskTimeZone,
      taskType: this.queryParams.taskType
    };
    workbenchMagnet(params).then((res) => {
      if (res.data) {
        this.magnetData = res.data;
      } else {
        this.$message.error('磁贴展示查询失败：' + res.error.message);
      }
    });
  }

  // 获取任务中心列表
  getWorkbenchList() {
    this.taskTagsList.map((item) => {
      this.queryParams.tags.push(item.tagName);
    });
    this.sceneTagsList.map((item) => {
      this.queryParams.sceneIds.push(item.tagName);
    });
    const params = {
      limit: this.limit,
      page: this.page,
      queryEndTime: this.dateValue[1],
      queryStartTime: this.dateValue[0],
      sceneIds: this.queryParams.sceneIds,
      tags: this.queryParams.tags,
      customColumns: this.queryParams.customColumns,
      taskTimeZone: this.queryParams.taskTimeZone,
      taskType: this.queryParams.taskType
    };
    this.loading = true;
    queryWorkbenchList(params).then((res) => {
      this.loading = false;
      if (res.data) {
        this.WorkbenchData = res.data.rows;
        this.total = res.data.total;
      } else {
        this.$message.error('任务中心列表查询失败!');
      }
    });
  }
  // 任务中心 改变页码
  handleCurrent(el: any) {
    this.page = el;
    this.getWorkbenchList();
  }
  // 任务中心 改变页大小
  handleSize(el: any) {
    this.limit = el;
    this.page = 1;
    this.getWorkbenchList();
  }
  // 任务中心 获取选中id
  selectionChange(row) {
    let rowId = '';
    row.forEach((item) => {
      rowId = item.id;
    });
    this.checkedRowId.push(rowId);
  }
  // 任务中心区间段改变
  // taskQuantumChange(val) {
  //   if (val) {
  //     this.getWorkbenchList();
  //   }
  // }
  // 请输入关键字搜索
  querySearch(queryString, cb) {
    const restaurants = this.restaurants;
    const results = queryString
      ? restaurants.filter(this.createFilter(queryString))
      : restaurants;
    // 调用 callback 返回建议列表的数据
    cb(results);
  }
  createFilter(queryString) {
    return (restaurant) => {
      return (
        restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      );
    };
  }
  // 搜索列名称 下拉事件
  lieHandleSelect(item) {
    this.taskCenterName = item.value;
  }
  loadAll() {
    return [
      { value: '三全鲜食（北新泾店）', address: '长宁区新渔路144号' },
      {
        value: 'Hot honey 首尔炸鸡（仙霞路）',
        address: '上海市长宁区淞虹路661号'
      },
      {
        value: '新旺角茶餐厅',
        address: '上海市普陀区真北路988号创邑金沙谷6号楼113'
      }
    ];
  }
  // 任务标签列表查询
  getTaskTagList() {
    this.taskTagList = [];
    // this.dateValue = JSON.parse(JSON.stringify(this.dateValue));
    const params = {
      limit: this.limit,
      page: this.page,
      queryEndTime: this.dateValue[1],
      queryStartTime: this.dateValue[0],
      sceneIds: this.queryParams.sceneIds,
      tags: [],
      customColumns: this.queryParams.customColumns,
      taskTimeZone: this.queryParams.taskTimeZone,
      taskType: this.queryParams.taskType
    };
    tagsList(params).then((res) => {
      if (res.data) {
        this.taskTagList = this.taskTagList.concat(res.data);
      } else {
        this.$message.error('任务标签列表查询失败：' + res.error.message);
      }
    });
  }
  // 任务标签点击
  tagSelectTag(tag: any) {
    if (tag.tagName === '全部') {
      if (this.taskTagsList.length === this.taskTagList.length) {
        this.taskTagsList = [];
      } else {
        this.taskTagsList = cloneDeep(this.taskTagList);
      }
    } else {
      const tagIndex = this.taskTagsList.findIndex(function(item) {
        return item.tagName === tag.tagName;
      });
      if (tagIndex === -1) {
        this.taskTagsList.push(tag);
        if (this.taskTagsList.length === this.taskTagList.length - 1) {
          this.taskTagsList.push({
            tagName: '全部',
            tagCount: this.total
          });
        }
      } else {
        this.taskTagsList.splice(tagIndex, 1);
        const allTag = this.taskTagsList.findIndex(function(item) {
          return item.tagName === '全部';
        });
        if (allTag !== -1) {
          this.taskTagsList.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }
  // 场景标签列表查询
  getSenceTagList() {
    this.sceneTagList = [];
    // this.dateValue = JSON.parse(JSON.stringify(this.dateValue));
    // const params = {
    //   limit: this.limit,
    //   page: this.page,
    //   queryEndTime: this.dateValue[1],
    //   queryStartTime: this.dateValue[0],
    //   sceneIds: [],
    //   tags: this.queryParams.tags,
    //   customColumns: this.queryParams.customColumns,
    //   taskTimeZone: this.queryParams.taskTimeZone,
    //   taskType: this.queryParams.taskType
    // };
    getTagCount().then((res) => {
      if (res.data) {
        this.sceneTagList = this.sceneTagList.concat(res.data);
      } else {
        this.$message.error('场景标签列表查询失败：' + res.error.message);
      }
    });
  }
  // 场景标签点击
  taskSceneTagList(tag: any) {
    if (tag.tagName === '全部') {
      if (this.sceneTagsList.length === this.sceneTagList.length) {
        this.sceneTagsList = [];
      } else {
        this.sceneTagsList = cloneDeep(this.sceneTagList);
      }
    } else {
      const tagIndex = this.sceneTagsList.findIndex(function(item) {
        return item.tagName === tag.tagName;
      });
      if (tagIndex === -1) {
        this.sceneTagsList.push(tag);
        if (this.sceneTagsList.length === this.sceneTagList.length - 1) {
          this.sceneTagsList.push({
            tagName: '全部',
            tagCount: this.total
          });
        }
      } else {
        this.sceneTagsList.splice(tagIndex, 1);
        const allTag = this.sceneTagsList.findIndex(function(item) {
          return item.tagName === '全部';
        });
        if (allTag !== -1) {
          this.sceneTagsList.splice(allTag, 1);
        }
      }
    }
    this.handleSearch();
  }

  // 搜索
  handleSearch() {
    this.page = 1;
    this.getWorkbenchList();
    this.getMagnet();
  }
  // 重置
  handleReset() {
    this.queryParams = {
      customColumns: '',
      sceneIds: [],
      tags: [],
      taskTimeZone: 0,
      taskType: 0
    };
    this.queryEndTime = '';
    this.queryStartTime = '';
    this.handleSearch();
  }
  // 高级查询
  advancedQuery() {
    this.isQueryShow = true;
    this.isQueryhide = false;
    this.isQueryTagShow = true;
  }
  // 收起查询
  packUpQuery() {
    this.isQueryShow = false;
    this.isQueryhide = true;
    this.isQueryTagShow = false;
  }

  // 批量传阅
  batchCirculate() {
    if (this.checkedRowId.length != 0) {
      this.queryUserInfo();
      this.batchCirculateDialog = true;
    } else {
      this.$message.error('至少选中一条数据！');
    }
  }
  // 批量传阅发起
  batchCirculateSave() {
    const params = {
      instanceIds: this.checkedRowId,
      selectUserId: this.batchCirculateVal
    };
    circulationTaskBatch(params).then((res) => {
      if (res.data) {
        this.$message.success('批量传阅发起成功！');
        this.batchCirculateDialog = false;
        this.batchCirculateVal = '';
        this.getWorkbenchList();
      } else {
        this.$message.error('批量传阅失败：' + res.error.message);
      }
    });
  }
  // 批量传阅取消
  batchCircularizeCancel() {
    this.batchCirculateVal = '';
    this.batchCirculateDialog = false;
  }
  // 批量完成
  batchFinish() {
    if (this.checkedRowId.length != 0) {
