  // 选择上传的文件
  doUpload() {
    if (this.importFiles.length === 0) {
      this.$message.error('请先选择要导入或上传的文件！');
    } else {
      this.importLoading = true;
      const uploadForm = new FormData();
      this.importFiles.forEach((item) => {
        uploadForm.append('file', item.raw);
      });
      upload(uploadForm).then((res) => {
        let filePath = '';
        if (res.data) {
          filePath = res.data.path;
          const params = {
            createTime: '',
            editTime: '',
            filePath: filePath,
            fileType: 0,
            id: '',
            instanceId: this.taskMsg.id,
            operationId: '',
            type: 0,
            uploadedUser: ''
          };
          recordFile(params).then((res) => {
            if (res.data) {
              this.$message.success('导入成功');
              this.importDialogVisible = false;
            }
          });
        } else {
          this.$message.error(`导入失败:${res.error}`);
        }
      });
      this.importLoading = false;
      // this.importDialogVisible = false;
    }
  }
