  created() {
    const date = new Date(); //获取新的时间(2020.8.31）
    const year = date.getFullYear().toString(); // 获取当前时间的年份转为字符串   '2020'
    const month =
      date.getMonth() + 1 < 10
        ? '0' + (date.getMonth() + 1).toString()
        : (date.getMonth() + 1).toString(); // 获取月份，由于月份从0开始，此处要加1，判断是否小于10，如果是在字符串前面拼接'0'   '08'
    const day =
      date.getDate() < 10
        ? '0' + date.getDate().toString()
        : date.getDate().toString(); //获取天，判断是否小于10，如果是在字符串前面拼接'0'   '12'
    const hh =
      date.getHours() < 10
        ? '0' + date.getHours().toString()
        : date.getHours().toString(); // 获取小时，判断是否小于10，如果是在字符串前面拼接'0'   '12'
    const mm =
      date.getMinutes() < 10
        ? '0' + date.getMinutes().toString()
        : date.getMinutes().toString(); // 获取分钟，判断是否小于10，如果是在字符串前面拼接'0'   '12'
    const ss =
      date.getSeconds() < 10
        ? '0' + date.getSeconds().toString()
        : date.getSeconds().toString(); //获取秒，判断是否小于10，如果是在字符串前面拼接'0'   '12'
    //字符串拼接，开始时间，结束时间
    const end = year + '-' + month + '-' + day + ' ' + hh + ':' + mm + ':' + ss; //当天'2019-04-12'
    const beg =
      year + '-' + month + '-01' + ' ' + '00' + ':' + '00' + ':' + '00'; //当月第一天'2019-04-01'
    this.dateValue = [beg, end]; //将值设置给插件绑定的数据
  }
